
.card-container{
    /* width:100vw; */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    margin:0 10px;
    /* height: 100px; */
    /* width: 200px; */
  }
  .carousel-img {
    border-radius: 20px;
    background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)),url(../img/ii.jpg);
    border:1px solid rgba(11, 153, 209, 0.987);
    background-size: 300px;
    width:25rem;
    height: 150px;
    transition: 1s;
    justify-content: center;
    align-items: center;
    display: flex;
    position: relative;
  
  }
  .carousel-img:hover  .carousel-content {
    display: block;
  }
  .carousel-img:hover{
    overflow: hidden;
    background-image: url(../img/ii.jpg) ;
  /* transform: scale(1.2); */
    /* backdrop-filter: blur(2px) */
  }
  .carousel-title{
    text-align: center;
    color:white;
    font-weight: bold;
  }
  .carousel-content{
    text-align: center;
    position: absolute;
    display: block;
    color:white;
    font-size: 12px;
  }
  .carousel-rating-count{
    font-size: 10px;
  }
  @media(min-width:574px){
    .carousel-img {
      border-radius: 20px;
      background-image: linear-gradient(rgba(0,0,0,0.5),rgba(0,0,0,0.5)), url(../img/ii.jpg);
      border:3px solid rgba(11, 153, 209, 0.987);
      background-size: 500px;
      width:450px;
      height: 300px;
      transition: 1s;
    background-position: center;
    }
    .carousel-img:hover  .carousel-content {
      display: block;
    }
    .carousel-content{
      text-align: center;
      position: absolute;
      display: block;
      color:white;
      font-size: 20px;
    }
  .carousel-rating-count{
font-size: 12px;
  }
}